* {
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

li {
  list-style: none;
}

img {
  vertical-align: top;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: inline-block;
}

.menu__list {
  display: flex;
}

.menu__item:not(:last-child) {
  margin-right: 20px;
}

.menu__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  padding: 22.5px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  transition: all .3s;
  display: inline-block;
  position: relative;
}

.menu__link:hover, .menu .is-active {
  color: #00acdc;
}

.menu .is-active:after {
  content: "";
  z-index: 1;
  background-color: #00acdc;
  border-radius: 4px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  font-weight: 400;
  transition: all .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  text-align: center;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  text-align: left;
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  display: block;
}

.card__title:hover .card__title {
  color: #34568b;
}

.card > p {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__raiting {
  justify-content: space-between;
  align-items: center;
  padding-bottom: 24px;
  display: flex;
}

.card__raiting > p {
  text-align: right;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.card__price :first-child {
  color: #616070;
  font-size: 12px;
}

.card__price :nth-child(2) {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card .stars {
  align-items: center;
  gap: 4px;
  display: flex;
}

.card .stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.card .stars .stars-active {
  background-image: url("star-active.edda4c43.svg");
}

.card .card__button {
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: background-color .3s;
  display: block;
}

.card .card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
}

html {
  font-family: Roboto, sans-serif;
}

main {
  grid-template-rows: repeat(2, 408px);
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 488px) {
  main {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  main {
    grid-template-columns: repeat(4, 200px);
  }
}

